import React, { Component } from "react";
import { Dark_Mode } from "../../../constants/colors";
import SearchIcon from "@material-ui/icons/Search";
import styles, { combineStyles } from "../../../constants/styles";
import { Card, Paper } from "@material-ui/core";
export default class Header extends Component {
  render() {
    return (
      <Card
        elevation={10}
        style={{
          height: "12%",
          width: "100%",
          alignItems: "center",
          display: "flex",
          flexDirection: "row",
          backgroundColor: styles.backgroundColor,
          borderRadius: 0,
          ...styles.bb1,
          borderColor: "grey",
        }}
      >
        <div
          style={{
            height: "100%",
            width: "10%",
            alignItems: "center",
            //  justifyContent: "center",
            display: "flex",
          }}
        >
          <img
            style={{
              width: "90%",
              height: "90%",
              borderRadius: "90%",
              marginLeft: 10,
              marginRight: 10,
              //margin: 10,
              //  marginRight: 20,
            }}
            src={require("../../../images/portrait.jpg")}
          />
        </div>
        <div
          style={{
            display: "flex",
            justifyContent: "space-evenly",
            flexDirection: "column",
            height: "100%",
            marginLeft: 10,
            overflow: "hidden",
          }}
        >
          <div
            style={{
              fontSize: "3vh",
              color: Dark_Mode ? styles.text_color : "rgb(60, 80, 90)",
            }}
          >
            Astitva Singhal
          </div>
          <div
            style={{
              fontSize: "2.5vh",
              color: Dark_Mode ? styles.text_color : "#61b7d5",
            }}
          >
            Online
          </div>
        </div>
        <div
          style={{
            flex: 1,
            justifyContent: "flex-end",
            display: "flex",
          }}
        >
          <div
            style={combineStyles([
              {
                backgroundColor: Dark_Mode ? "transparent" : "#61b7d5",
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                borderRadius: 100,
                padding: 5,
              },

              styles.m2,
            ])}
          >
            <SearchIcon
              //fontSize="large"
              style={combineStyles([
                { color: Dark_Mode ? "grey" : "#ffffff", fontSize: 30 },
              ])}
            />
          </div>
        </div>
      </Card>
    );
  }
}
