import React, { Component } from "react";
import { Color_Theme, Dark_Color, Dark_Mode } from "../../constants/colors";
import { Card } from "@material-ui/core";
import s from "../../constants/styles";
import faker from "faker";
export default class ChatList extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <Card
        elevation={10}
        style={{
          width: "25%",
          height: "100%",
          //borderRight: "solid black 1px",
          overflow: "scroll",
          scrollbarColor: Dark_Mode
            ? "rgb(80, 99, 107)"
            : "rgba(84, 177, 210,0.8)",
          scrollbarWidth: "thin",
          backgroundColor: Dark_Mode
            ? "rgba(10, 15, 15,0.8)"
            : "rgba(84, 177, 210,0.8)",
          overflowX: "hidden",
        }}
      >
        {this.props.list.map((item) => {
          return (
            <div
              style={{
                display: "flex",
                flexDirection: "row",
                alignItems: "center",
                borderBottom: "solid  0.1px",
                borderColor: Dark_Mode ? "grey" : "#ffffff",
              }}
            >
              <img
                style={{
                  width: "75%",
                  height: "75%",
                  maxHeight: 75,
                  maxWidth: 75,
                  borderRadius: "50%",
                  margin: 10,
                  //  marginRight: 20,
                }}
                src={faker.image.avatar()}
              />
              <div
                style={{
                  overflow: "hidden",
                  textOverflow: "ellipsis",
                  padding: 10,
                  //  border: "solid black 1px",
                  justifyContent: "center",
                  display: "flex",
                  flexDirection: "column",
                  //  alignItems: "center",
                }}
              >
                <div
                  style={{
                    flex: 1,
                    color: "white",
                    fontSize: "3vh",
                    marginBottom: 10,
                  }}
                >
                  {item.name}
                </div>
                <div
                  style={{
                    flex: 1,
                    color: s.text_color,
                    width: "100%",
                    overflow: "hidden",
                    textOverflow: "ellipsis",
                    fontSize: "2.5vh",
                  }}
                >
                  {faker.lorem.words()}
                </div>
              </div>
            </div>
          );
        })}
      </Card>
    );
  }
}
