import React, { Component } from "react";
import s, { combineStyles } from "../../../constants/styles";
import { TextField, Card } from "@material-ui/core";
import InsertEmoticon from "@material-ui/icons/InsertEmoticon";
import AttachFile from "@material-ui/icons/AttachFile";
import Send from "@material-ui/icons/Send";
import { Button_Color, Dark_Mode } from "../../../constants/colors";
export default class MessageInput extends Component {
  constructor(props) {
    super(props);
    this.state = {
      message: "",
    };
  }
  handleInput = (e) => {
    var x = e.target.value;
    this.setState({ message: x });
    //document.getElementById("demo").innerHTML = "You wrote: " + x;
  };
  handleSend = () => {
    console.log(this.state.message);
    //send message
  };
  render() {
    const { message } = this.state;
    return (
      <div
        style={{
          width: "100%",
          backgroundColor: s.backgroundColor,
          display: "flex",
          alignItems: "center",
        }}
      >
        <div
          onClick={this.handleSend}
          style={combineStyles([
            {
              backgroundColor: Dark_Mode ? "transparent" : "#61b7d5",
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              borderRadius: 100,
              padding: 5,
            },

            s.m2,
          ])}
        >
          <InsertEmoticon
            //fontSize="large"
            style={combineStyles([
              { color: Dark_Mode ? "grey" : "#ffffff", fontSize: 30 },
            ])}
          />
        </div>
        <Card
          style={combineStyles([
            s.b1,
            s.m2,
            s.p2,
            {
              borderRadius: 10,
              width: "80%",
              flex: 1,
              backgroundColor: Dark_Mode ? "rgba(59, 59, 61,0.4)" : "#ffffff",
              borderColor: "grey",
            },
          ])}
        >
          <textarea
            id="input"
            onChange={this.handleInput}
            style={{
              height: "3vh",
              width: "100%",
              color: Dark_Mode ? "rgb(211, 211, 211)" : "rgb(80, 99, 107)",
              backgroundColor: "transparent",
              border: "none",
              fontSize: "2.5vh",
            }}
            multiline={true}
          />
        </Card>
        {/*<AttachFile
          fontSize="large"
          style={combineStyles([s.m2, { color: "grey" }])}
        />*/}
        {/*<div>
          <Send
            fontSize="large"
            style={combineStyles([s.m2, { color: "grey" }])}
          />
        </div>*/}

        <div
          onClick={this.handleSend}
          style={combineStyles([
            {
              backgroundColor: Dark_Mode ? "transparent" : "#61b7d5",
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              borderRadius: 100,
              padding: 5,
            },

            s.m2,
          ])}
        >
          <AttachFile
            //fontSize="large"
            style={combineStyles([
              { color: Dark_Mode ? "grey" : "#ffffff", fontSize: 30 },
            ])}
          />
        </div>
        <div
          onClick={this.handleSend}
          style={combineStyles([
            {
              backgroundColor: Dark_Mode ? "transparent" : "#61b7d5",
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              borderRadius: 100,
              padding: 5,
            },

            s.m2,
          ])}
        >
          <Send
            //fontSize="large"
            style={combineStyles([
              { color: Dark_Mode ? "grey" : "#ffffff", fontSize: 30 },
            ])}
          />
        </div>
      </div>
    );
  }
}
