export default class MessageList extends Component {
  render() {
    return (
      <div
        style={{
          flex: 1,
          overflowY: "scroll",
          scrollbarColor: "grey",
          scrollbarWidth: "thin",
          backgroundColor: s.backgroundColor,
          width: "100%",
        }}
      >
        {messages.map((item) => {
          return (
            <div
              style={combineStyles([
                //s.p2,
                s.m2,
                { display: "flex", flexDirection: "column" },
                //s.b1,
              ])}
            >
              <Paper
                elevation={1}
                style={combineStyles([
                  {
                    alignSelf:
                      item.sender == "Astitva" ? "flex-end" : "flex-start",
                    borderRadius: 10,
                    borderTopLeftRadius: item.sender == "Astitva" ? 10 : 0,
                    borderTopRightRadius: item.sender == "Astitva" ? 0 : 10,
                    fontSize: "2.5vh",
                    color:
                      item.sender != "Astitva"
                        ? Dark_Mode
                          ? "rgb(211, 211, 211)"
                          : "rgb(80, 99, 107)"
                        : Dark_Mode
                        ? "rgb(144, 144, 146)"
                        : "#ffffff",
                    backgroundColor:
                      item.sender != "Astitva"
                        ? Dark_Mode
                          ? "rgb(65, 65, 65)"
                          : "#ffffff"
                        : Dark_Mode
                        ? "rgb(34, 34, 34)"
                        : "rgb(97, 183, 213)",
                  },
                  s.b0,
                  s.p2,
                ])}
              >
                {item.message}
              </Paper>
            </div>
          );
        })}
      </div>
    );
  }
}
